import React from 'react';
import close_ from '../assets/close.png'

export default function VideoPlayer({close}) {
 return(
  <div className="video_player" onClick={()=>close()}>
   <img className="close_video" src={close_} alt="close" />
   <iframe  width="80%" height="62%" src="https://www.youtube.com/embed/6dWsskCqVu0?autoplay=1" allow='autoplay' frameborder="0" >
    
  </iframe >
  </div>
 )
}