import React, { useEffect, useState } from "react";

import ticket from "../assets/ticket.png";
import link from "../assets/link.png";
import bow_tie from "../assets/dress_icon.png";
import location from "../assets/placeholder.png";
import close_ from '../assets/close.png'
import dress_code from '../assets/dress_code.jpeg';


export default function Invitation({ invitado }) {
  const [counter, setCounter] = useState({
    day: 0,
    hour: 0,
    min: 0,
    sec: 0,
  });
  const [dressCode, setDressCode] = useState(false);
  const [gift, setGift] = useState(false);

  const virtualBtn =
    invitado[0].Modalidad === "PRESENCIAL"
      ? ["Dress Code", "Ubicación", "Regalos"]
      : ["Link de transmisión", "Regalos"];
  const virtualIcons =
    invitado[0].Modalidad === "PRESENCIAL"
      ? [bow_tie, location, ticket]
      : [link, ticket];

  var pastDate = new Date(
    `Jul 9, 2021 ${
      invitado[0].Modalidad === "PRESENCIAL" ? "16:00:00" : "16:30:00"
    }`
  ).getTime();
  function pastTime() {
    var now = new Date().getTime();
    var gap = pastDate - now;
    var sec = 1000;
    var min = sec * 60;
    var hour = min * 60;
    var day = hour * 24;

    var d = Math.floor(gap / day);
    var h = Math.floor(gap % day) / hour;
    var m = Math.floor(gap % hour) / min;
    var s = Math.floor(gap % min) / sec;

    setCounter({
      day: Math.trunc(d),
      hour: Math.trunc(h),
      min: Math.trunc(m),
      sec: Math.trunc(s),
    });
  }

  setInterval(() => {
    pastTime();
  }, 1000);

  return (
    <div className="all_invitation">
      { !gift && !dressCode &&
        <div className="invitation_container">
          <div className="date_time_container">
            <span className="date_start">
              <p>09</p>
              <p>JULIO</p>
            </span>
            <span className="line_divide"></span>
            <span className="time_Start">
              <p>04</p>
              <p>{invitado[0].Modalidad === "PRESENCIAL" ? "00" : "30"} pm</p>
            </span>
          </div>

          <div className="date_counter_container">
            <div className="number_container">
              {counter.day}
              <span>Días</span>
            </div>
            <div className="number_container">
              {counter.hour}
              <span>Horas</span>
            </div>
            <div className="number_container">
              {counter.min}
              <span>Minutos</span>
            </div>
            <div className="number_container">
              {counter.sec}
              <span>Segundos</span>
            </div>
          </div>

          <div className="we_got_married">¡Nos Casamos!</div>

          <div className="btn_interact_container">
            {virtualBtn.map((btn, index) => {
              return (
                <div
                  key={index}
                  className="btn_card"
                  onClick={
                    index === 0 && invitado[0].Modalidad === "PRESENCIAL"
                      ? () => {
                          setDressCode(true);
                          setGift(false);
                          console.log("hola");
                        }
                      : index === 2 && invitado[0].Modalidad === "PRESENCIAL"
                      ? () => {
                          setGift(true);
                          setDressCode(false);
                        }
                      : index === 0 && invitado[0].Modalidad !== "PRESENCIAL"
                      ? null
                      : index === 1 && invitado[0].Modalidad !== "PRESENCIAL"
                      ? ()=> setGift(true) : null
                  }
                >
                  {index === 1 && invitado[0].Modalidad === "PRESENCIAL" ?
                    <a href="https://maps.app.goo.gl/R9HvrXYeDwYMGmNj6" target="_blank">{btn}</a>
                    :
                    index === 0 ?
                    <a className="live" href="https://www.youtube.com/watch?v=WPWuPUJ8I10" target="_blank">{btn}</a>
                    :
                    <p>{btn}</p>

                  }
                  <img src={virtualIcons[index]} alt="" />
                </div>
              );
            })}
          </div>
        </div>
      }
      {dressCode === true && 
        <div className="dress_n_gift_container" onClick={()=>{setGift(false);setDressCode(false);}}>
          <img className="close_video" src={close_} alt="close" />
          <img className="dress_code" src={dress_code} alt="dress_code" />
          <div>
          Hombres: Formal playero
          Mujeres: Vestido largo de flores
          </div>
        </div>
      }
      {gift === true && 
        <div className="dress_n_gift_container"  onClick={()=>{setGift(false);setDressCode(false);}}>
          <img className="close_video" src={close_} alt="close" />
          <div className="card_bank_list"> 
            <div className="count_info">
              <p>Bancolombia: Ahorros</p>
              <p>No 48371739532</p>
              <p>CC. 1140885128</p>
              <p>Rodrigo De La Ossa</p>
              

            </div>
            <div className="count_info">
              <p>Banco de Bogota: Ahorros</p>
              <p>No 096148531</p>
              <p>CC. 1140881677</p>
              <p>Andrea Angulo</p>
              
            </div>
            <div className="count_info">
              <p>Nequi</p>
              <p>3164977217</p>
              <p>CC. 1140885128</p>
              <p>Andrea Angulo</p>
              
            </div>
            <div className="count_info" style={{borderBottom:"2px solid transparent"}}>
              <p>DaviPlata</p>
              <p>3164977217</p>
              <p>CC. 1140885128</p>
              <p>Andrea Angulo</p>
              
            </div>

          </div>
        </div>
      }
    </div>
  );
}
