import React, {useState} from 'react';
import {invitados} from './dataList'
import VideoPlayer from './VideoPlayer';
import Invitation from './Card2';

import send from '../assets/send.png';
import prev from '../assets/prev.jpg';
import play_ from '../assets/play.png';
import invitation from '../assets/invitation.png';
import back from '../assets/back.png';

export default function Card1({reset,counter}) {
 const [code, setCode] = useState(null);
 const [invitado, setInvitado] = useState(null)
 const [play, setPlay] = useState(false);
 const [goInvitation, setGoInvitation] = useState(false)

 function filterCode() {
  if (code === null || code.length === 0) {
   alert("No ha ingresado su código")
   return
  }else{
   let invs = invitados.filter(inv => inv.Codigo === code.toString())
   if (invs.length === 0) {
    alert("Su código no coincide, por favor intente de nuevo")
   }else {
    setInvitado(invs)
   }
  }
  
 }
 
 function closeVideo() {
  setPlay(false)
 }

 return(
  <div className="card_1_container">
    <span 
      className="back_card1" 
      onClick={goInvitation?()=>setGoInvitation(false):invitado!==null? ()=>setInvitado(null):counter === 8 ? ()=> reset():null}
    >
      <img src={back} alt="back" />
    </span>
   {invitado === null && 
    <div className="container_input_msg">
     <div className="container_code_input">
      <div className="container_input">
       <input onChange={(e)=>setCode(e.target.value)} id="input_code" className="input_code" type="text" required/>
      </div>
      <div className="btn_send" onClick={()=>filterCode()}>
       <img className="" src={send} alt="send"/>
      </div>
     </div>
      <p>Ingrese código</p>
    </div>
   }
   {invitado !== null && invitado.length > 0 && !goInvitation && 
    <div className="second_card_content">
     <div className="text_msg_container">
      <p id="group_name">{invitado[0].Grupo}</p>
      <p>¡Tenemos un mensaje especial!</p>
     </div>
     <div className="video_container">
      <img src={prev} alt="prev" />
      <div className="video_back" onClick={()=>setPlay(true)}>
       <img src={play_} alt="play"/>
      </div>
  
     </div>
     <div className="go_to_invitation">
      <div className="text_icon_container" onClick={()=>setGoInvitation(true)}>
       
        {invitado[0].Modalidad === "PRESENCIAL"?
        <p>
         cupo (<b>{invitado[0].Cupo}</b>)
        </p>
        :
        <p></p>

        }
       <div>
       <p>Ver invitación</p>
       <img src={invitation} alt="inv"/>
       </div>
      </div>
     </div>
    </div>

   }

   {play && 
    <VideoPlayer
     close={()=>closeVideo()}
    />

   }

   {goInvitation &&
    <Invitation
     invitado={invitado}
    />
   }
   
  </div>
 
 )
}