import React, { useState, useEffect } from "react";
import Card0 from "./Card0";
import Card1 from "./Card1";

export default function CardsContainer() {
 const [counter, setCounter] = useState(1);

 useEffect(() => {
  timer()
 }, []);

 function timer() {
  var timeleft = 8;
  var downloadTimer = setInterval(function () {
   if (timeleft <= 0) {
    clearInterval(downloadTimer);
   }
   let rest = 8 - timeleft
   if (rest>0) {
    setCounter(rest)
   }
   timeleft -= 1;
  }, 1000);
 }

 function resetTime() {
  setCounter(1);
  timer();
 }

 console.log("ounter", counter);

 return (
  <div className={counter < 8? "card_container_init":"card_container_next"}>
  { counter < 8 && <p className="counter">{counter}</p>}
  { counter < 8 &&  <Card0/>}
  {counter === 8 && <Card1 reset={()=>resetTime()} counter={counter}/>}
  </div>
 );
}
